.App {
  font-family: 'Montserrat', sans-serif;
  display: -ms-flexbox;
  display: flex;
  background-color: #2F80ED;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  margin: 0 auto;
}


.mainBlock{
  display: -ms-flexbox;
  display: flex;
  min-width: 500px;
  width: 90%;
  height: 90%;
  margin: 0 auto ;
  background:#333333;
  border-radius: 20px;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-shadow: 2px 2px 15px 3px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 2px 15px 3px rgba(0, 0, 0, 0.3);
}


@media screen and (max-width:600px){
  .mainBlock{
    min-width: 300px;
    width: 90%;
    height: 70%;
    background:#333333;
    border-radius: 20px;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
  }
  .searchButton{
    margin-top: 20px;
    margin-left: 0px;
  }

  .wordInput{
    font-size: 25px !important;
  }

  .wordMeaning{
    font-size: 1.5em !important;
  }

  .pronounciation {
    font-size: 1.2 em !important;
  }

}

form {
  margin-top: 50px;
    text-align: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}


.wordInput {
  text-align: center;
  font-size:30px;
  width: 300px;
  height: 50px;
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px gray;
  outline: none;
  color: white;
  background: #333333
}

