.searchButton{
  width: 100px;
  height: 35px;
  margin-left: 20px;
  border-radius:3px;
  background: #FF0057;
  border: 0;
  color: white;
  font-weight: 100;
  font-size: 14px;
  z-index: 11;
  -webkit-box-shadow: 4px 2px 17px 4px rgba(214, 4, 76, 0.31);
          box-shadow: 4px 2px 17px 4px rgba(214, 4, 76, 0.31);
}


/* .searchButton:hover {
  width:120px;
  height: 40px;
} */

.inputDiv {
  -ms-flex-positive: 4;
      flex-grow: 4;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  margin: auto auto;
}

.outputDiv {
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  position: relative;
  max-height: 100%;
  -ms-flex-positive:7;
      flex-grow:7;
  -ms-flex-align: center;
      align-items: center;
  margin: auto auto auto auto;
  -ms-flex-pack: center;
      justify-content: center;
 
}

.wordMeaning{
  position: relative;
  text-align: center;
  vertical-align:center;
  font-size: 2em;
  color: white;
  margin: 50px;
}

.error{
  color: red
}

.pronounciation{
  position: relative;
  text-align: center;
  vertical-align:center;
  font-size: 1.9em;
  color:gray;
  margin: 50px;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #FF0057; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


